'use strict';

// MODULES //

import stride2offset from '@stdlib/strided/base/stride2offset';
import ndarray from './ndarray.js';


// MAIN //

/**
* Computes the L2-norm of a double-precision floating-point vector.
*
* @param {PositiveInteger} N - number of indexed elements
* @param {Float64Array} x - input array
* @param {integer} stride - stride length
* @returns {number} L2-norm
*
* @example
* import Float64Array from '@stdlib/array/float64';
*
* var x = new Float64Array( [ 1.0, -2.0, 2.0 ] );
*
* var out = dnrm2( 3, x, 1 );
* // returns 3.0
*/
function dnrm2( N, x, stride ) {
	var ox = stride2offset( N, stride );
	return ndarray( N, x, stride, ox );
}


// EXPORTS //

export default dnrm2;
