'use strict';

/**
* Compute the sum of double-precision floating-point strided array elements, ignoring `NaN` values and using ordinary recursive summation.
*
* @module @stdlib/blas/ext/base/dnannsumors
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import dnannsumors from '@stdlib/blas/ext/base/dnannsumors';
*
* var x = new Float64Array( [ 1.0, -2.0, NaN, 2.0 ] );
* var out = new Float64Array( 2 );
*
* var v = dnannsumors( x.length, x, 1, out, 1 );
* // returns <Float64Array>[ 1.0, 3 ]
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import dnannsumors from '@stdlib/blas/ext/base/dnannsumors';
*
* var x = new Float64Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0, NaN, NaN ] );
* var out = new Float64Array( 2 );
*
* var v = dnannsumors.ndarray( 5, x, 2, 1, out, 1, 0 );
* // returns <Float64Array>[ 5.0, 4 ]
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils/try-require';
import isError from '@stdlib/assert/is-error';
import main from './main.js';


// MAIN //

var dnannsumors;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	dnannsumors = main;
} else {
	dnannsumors = tmp;
}


// EXPORTS //

export default dnannsumors;

// exports: { "ndarray": "dnannsumors.ndarray" }
