'use strict';

// MODULES //

import float64ToFloat32 from '@stdlib/number/float64/base/to-float32';
import Complex64 from './../../../../float32/ctor';
import realf from './../../../../float32/real';
import imagf from './../../../../float32/imag';


// MAIN //

/**
* Adds two single-precision complex floating-point numbers.
*
* @param {Complex64} z1 - complex number
* @param {Complex64} z2 - complex number
* @returns {Complex64} result
*
* @example
* import Complex64 from '@stdlib/complex/float32/ctor';
* import realf from '@stdlib/complex/float32/real';
* import imagf from '@stdlib/complex/float32/imag';
*
* var z = new Complex64( 5.0, 3.0 );
* // returns <Complex64>
*
* var out = add( z, z );
* // returns <Complex64>
*
* var re = realf( out );
* // returns 10.0
*
* var im = imagf( out );
* // returns 6.0
*/
function add( z1, z2 ) {
	var re = float64ToFloat32( realf( z1 ) + realf( z2 ) );
	var im = float64ToFloat32( imagf( z1 ) + imagf( z2 ) );
	return new Complex64( re, im );
}


// EXPORTS //

export default add;
