'use strict';

/**
* Simultaneously sort two double-precision floating-point strided arrays based on the sort order of the first array using heapsort.
*
* @module @stdlib/blas/ext/base/dsort2hp
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import dsort2hp from '@stdlib/blas/ext/base/dsort2hp';
*
* var x = new Float64Array( [ 1.0, -2.0, 3.0, -4.0 ] );
* var y = new Float64Array( [ 0.0, 1.0, 2.0, 3.0 ] );
*
* dsort2hp( x.length, 1.0, x, 1, y, 1 );
*
* console.log( x );
* // => <Float64Array>[ -4.0, -2.0, 1.0, 3.0 ]
*
* console.log( y );
* // => <Float64Array>[ 3.0, 1.0, 0.0, 2.0 ]
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import dsort2hp from '@stdlib/blas/ext/base/dsort2hp';
*
* var x = new Float64Array( [ 1.0, -2.0, 3.0, -4.0 ] );
* var y = new Float64Array( [ 0.0, 1.0, 2.0, 3.0 ] );
*
* dsort2hp( x.length, 1.0, x, 1, 0, y, 1, 0 );
*
* console.log( x );
* // => <Float64Array>[ -4.0, -2.0, 1.0, 3.0 ]
*
* console.log( y );
* // => <Float64Array>[ 3.0, 1.0, 0.0, 2.0 ]
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils/try-require';
import isError from '@stdlib/assert/is-error';
import main from './main.js';


// MAIN //

var dsort2hp;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	dsort2hp = main;
} else {
	dsort2hp = tmp;
}


// EXPORTS //

export default dsort2hp;

// exports: { "ndarray": "dsort2hp.ndarray" }
