'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name base
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/blas/base}
*/
import base from './../base';
setReadOnly( ns, 'base', base );

/**
* @name ddot
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ddot}
*/
import ddot from './../ddot';
setReadOnly( ns, 'ddot', ddot );

/**
* @name dswap
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/dswap}
*/
import dswap from './../dswap';
setReadOnly( ns, 'dswap', dswap );

/**
* @name ext
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/blas/ext}
*/
import ext from './../ext';
setReadOnly( ns, 'ext', ext );

/**
* @name gdot
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/gdot}
*/
import gdot from './../gdot';
setReadOnly( ns, 'gdot', gdot );

/**
* @name gswap
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/gswap}
*/
import gswap from './../gswap';
setReadOnly( ns, 'gswap', gswap );

/**
* @name sdot
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/sdot}
*/
import sdot from './../sdot';
setReadOnly( ns, 'sdot', sdot );

/**
* @name sswap
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/sswap}
*/
import sswap from './../sswap';
setReadOnly( ns, 'sswap', sswap );


// EXPORTS //

export default ns;
