'use strict';

/**
* Compute the cumulative sum of single-precision floating-point strided array elements using a second-order iterative KahanBabuka algorithm.
*
* @module @stdlib/blas/ext/base/scusumkbn2
*
* @example
* import Float32Array from '@stdlib/array/float32';
* import scusumkbn2 from '@stdlib/blas/ext/base/scusumkbn2';
*
* var x = new Float32Array( [ 1.0, -2.0, 2.0 ] );
* var y = new Float32Array( x.length );
*
* scusumkbn2( x.length, 0.0, x, 1, y, 1 );
* // y => <Float32Array>[ 1.0, -1.0, 1.0 ]
*
* @example
* import Float32Array from '@stdlib/array/float32';
* import scusumkbn2 from '@stdlib/blas/ext/base/scusumkbn2';
*
* var x = new Float32Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
* var y = new Float32Array( x.length );
*
* scusumkbn2.ndarray( 4, 0.0, x, 2, 1, y, 1, 0 );
* // y => <Float32Array>[ 1.0, -1.0, 1.0, 5.0, 0.0, 0.0, 0.0, 0.0 ]
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils/try-require';
import isError from '@stdlib/assert/is-error';
import main from './main.js';


// MAIN //

var scusumkbn2;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	scusumkbn2 = main;
} else {
	scusumkbn2 = tmp;
}


// EXPORTS //

export default scusumkbn2;

// exports: { "ndarray": "scusumkbn2.ndarray" }
