'use strict';

/**
* Reverse a double-precision floating-point strided array in-place.
*
* @module @stdlib/blas/ext/base/drev
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import drev from '@stdlib/blas/ext/base/drev';
*
* var x = new Float64Array( [ -2.0, 1.0, 3.0, -5.0, 4.0, 0.0, -1.0, -3.0 ] );
*
* drev( x.length, x, 1 );
* // x => <Float64Array>[ -3.0, -1.0, 0.0, 4.0, -5.0, 3.0, 1.0, -2.0 ]
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import drev from '@stdlib/blas/ext/base/drev';
*
* var x = new Float64Array( [ -2.0, 1.0, 3.0, -5.0, 4.0, 0.0, -1.0, -3.0 ] );
*
* drev( x.length, x, 1, 0 );
* // x => <Float64Array>[ -3.0, -1.0, 0.0, 4.0, -5.0, 3.0, 1.0, -2.0 ]
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils/try-require';
import isError from '@stdlib/assert/is-error';
import main from './main.js';


// MAIN //

var drev;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	drev = main;
} else {
	drev = tmp;
}


// EXPORTS //

export default drev;

// exports: { "ndarray": "drev.ndarray" }
