'use strict';

/**
* Perform a multiply-add operation involving three double-precision complex floating-point numbers.
*
* @module @stdlib/complex/float64/base/mul-add
*
* @example
* import Complex128 from '@stdlib/complex/float64/ctor';
* import real from '@stdlib/complex/float64/real';
* import imag from '@stdlib/complex/float64/imag';
* import muladd from '@stdlib/complex/float64/base/mul-add';
*
* var z1 = new Complex128( 5.0, 3.0 );
* // returns <Complex128>
*
* var z2 = new Complex128( -2.0, 1.0 );
* // returns <Complex128>
*
* var z3 = new Complex128( 7.0, -8.0 );
* // returns <Complex128>
*
* var out = muladd( z1, z2, z3 );
* // returns <Complex128>
*
* var re = real( out );
* // returns -6.0
*
* var im = imag( out );
* // returns -9.0
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import assign from './assign.js';
import strided from './strided.js';


// MAIN //

setReadOnly( main, 'assign', assign );
setReadOnly( main, 'strided', strided );


// EXPORTS //

export default main;

// exports: { "assign": "main.assign", "strided": "main.strided" }
