'use strict';

/**
* Fill a double-precision floating-point strided array with a specified scalar constant.
*
* @module @stdlib/blas/ext/base/dfill
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import dfill from '@stdlib/blas/ext/base/dfill';
*
* var x = new Float64Array( [ -2.0, 1.0, 3.0, -5.0, 4.0, 0.0, -1.0, -3.0 ] );
*
* dfill( x.length, 5.0, x, 1 );
* // x => <Float64Array>[ 5.0, 5.0, 5.0, 5.0, 5.0, 5.0, 5.0, 5.0 ]
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import dfill from '@stdlib/blas/ext/base/dfill';
*
* var x = new Float64Array( [ -2.0, 1.0, 3.0, -5.0, 4.0, 0.0, -1.0, -3.0 ] );
*
* dfill.ndarray( x.length, 5.0, x, 1, 0 );
* // x => <Float64Array>[ 5.0, 5.0, 5.0, 5.0, 5.0, 5.0, 5.0, 5.0 ]
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils/try-require';
import isError from '@stdlib/assert/is-error';
import main from './main.js';


// MAIN //

var dfill;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	dfill = main;
} else {
	dfill = tmp;
}


// EXPORTS //

export default dfill;

// exports: { "ndarray": "dfill.ndarray" }
