'use strict';

// MODULES //

import Float64Array from './../../float64';
import Float32Array from './../../float32';
import Int16Array from './../../int16';
import Int32Array from './../../int32';
import Int8Array from './../../int8';
import Uint16Array from './../../uint16';
import Uint32Array from './../../uint32';
import Uint8Array from './../../uint8';
import Uint8ClampedArray from './../../uint8c';
import Complex64Array from './../../complex64';
import Complex128Array from './../../complex128';
import BooleanArray from './../../bool';


// MAIN //

// Mapping from data types to constructors...
var ctors = {
	'float64': Float64Array,
	'float32': Float32Array,
	'generic': Array, // TODO: replace with `stdlib` pkg
	'int16': Int16Array,
	'int32': Int32Array,
	'int8': Int8Array,
	'uint16': Uint16Array,
	'uint32': Uint32Array,
	'uint8': Uint8Array,
	'uint8c': Uint8ClampedArray,
	'complex64': Complex64Array,
	'complex128': Complex128Array,
	'bool': BooleanArray
};


// EXPORTS //

export default ctors;
