'use strict';

/**
* Sort a double-precision floating-point strided array using heapsort.
*
* @module @stdlib/blas/ext/base/dsorthp
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import dsorthp from '@stdlib/blas/ext/base/dsorthp';
*
* var x = new Float64Array( [ 1.0, -2.0, 3.0, -4.0 ] );
*
* dsorthp( x.length, 1.0, x, 1 );
* // x => <Float64Array>[ -4.0, -2.0, 1.0, 3.0 ]
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import dsorthp from '@stdlib/blas/ext/base/dsorthp';
*
* var x = new Float64Array( [ 1.0, -2.0, 3.0, -4.0 ] );
*
* dsorthp.ndarray( x.length, 1.0, x, 1, 0 );
* // x => <Float64Array>[ -4.0, -2.0, 1.0, 3.0 ]
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils/try-require';
import isError from '@stdlib/assert/is-error';
import main from './main.js';


// MAIN //

var dsorthp;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	dsorthp = main;
} else {
	dsorthp = tmp;
}


// EXPORTS //

export default dsorthp;

// exports: { "ndarray": "dsorthp.ndarray" }
