'use strict';

/**
* Add a scalar constant to each double-precision floating-point strided array element and compute the sum using a second-order iterative KahanBabuka algorithm.
*
* @module @stdlib/blas/ext/base/dapxsumkbn2
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import dapxsumkbn2 from '@stdlib/blas/ext/base/dapxsumkbn2';
*
* var x = new Float64Array( [ 1.0, -2.0, 2.0 ] );
*
* var v = dapxsumkbn2( x.length, 5.0, x, 1 );
* // returns 16.0
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import dapxsumkbn2 from '@stdlib/blas/ext/base/dapxsumkbn2';
*
* var x = new Float64Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
*
* var v = dapxsumkbn2.ndarray( 4, 5.0, x, 2, 1 );
* // returns 25.0
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils/try-require';
import isError from '@stdlib/assert/is-error';
import main from './main.js';


// MAIN //

var dapxsumkbn2;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	dapxsumkbn2 = main;
} else {
	dapxsumkbn2 = tmp;
}


// EXPORTS //

export default dapxsumkbn2;

// exports: { "ndarray": "dapxsumkbn2.ndarray" }
