'use strict';

/**
* Multiply two single-precision complex floating-point numbers.
*
* @module @stdlib/complex/float32/base/mul
*
* @example
* import Complex64 from '@stdlib/complex/float32/ctor';
* import realf from '@stdlib/complex/float32/real';
* import imagf from '@stdlib/complex/float32/imag';
* import mul from '@stdlib/complex/float32/base/mul';
*
* var z1 = new Complex64( 5.0, 3.0 );
* // returns <Complex64>
*
* var z2 = new Complex64( -2.0, 1.0 );
* // returns <Complex64>
*
* var out = mul( z1, z2 );
* // returns <Complex64>
*
* var re = realf( out );
* // returns -13.0
*
* var im = imagf( out );
* // returns -1.0
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import assign from './assign.js';
import strided from './strided.js';


// MAIN //

setReadOnly( main, 'assign', assign );
setReadOnly( main, 'strided', strided );


// EXPORTS //

export default main;

// exports: { "assign": "main.assign", "strided": "main.strided" }
